function randomInArray(arr) {
    return arr[Math.floor(Math.random() * arr.length)];
}

export function genTiles() {
    let type = Math.floor(Math.random() * 2);
    let [block, box, point, coin, ground, player] = [
        randomInArray(TILES.block[type]),
        randomInArray(TILES.box[type]),
        randomInArray(TILES.point[type]),
        randomInArray(TILES.coin),
        randomInArray(TILES.ground),
        {
            up: TILES.player.up[type],
            down: TILES.player.down[type],
            left: TILES.player.left[type],
            right: TILES.player.right[type]
        }
    ];

    return {
        block, // 2
        box, // 3  .
        point, // 4
        coin,
        ground, // 1
        player // 5 .
    };
}

let index = 0;
function getCharactorTile(player, playerOrientation) {
    if (index >= player.up.length) {
        index = 0;
    }
    // console.log(player.up[index], playerOrientation);
    switch (playerOrientation) {
        case PlayerOrientation.up:
            return player.up[index++];
        case PlayerOrientation.down:
            return player.down[index++];
        case PlayerOrientation.left:
            return player.left[index++];
        case PlayerOrientation.right:
            return player.right[index++];
        default:
            return player.down[index++];
    }
}

export function dataToTile(obj, tiles, orientation) {
    let re;
    switch (obj) {
        case ObjType.void:
            re = '#fff';
            break;
        case ObjType.ground:
            re = tiles.ground;
            break;
        case ObjType.block:
            re = tiles.block;
            break;
        case ObjType.box:
            re = [tiles.box, tiles.ground];
            break;
        case ObjType.point:
            re = [tiles.point, tiles.ground];
            break;
        case ObjType.player:
            re = [getCharactorTile(tiles.player, orientation), tiles.ground];
            break;
        case ObjType.point + ObjType.box:
            re = [tiles.box, tiles.point, tiles.ground];
            break;
        case ObjType.point + ObjType.player: // ObjType.point + ObjType.player
            re = [
                getCharactorTile(tiles.player, orientation),
                tiles.point,
                tiles.ground
            ];
            break; // always remember the break in last case!
        default:
            re = '';
            break;
    }
    return re;
}
